.pokemon-list {
  width: 100vw;
  position: relative;
  padding: 10px;
  background-color: #ddd;
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 10px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

@media all and (min-width: 400px) {
  .pokemon-list {
    grid-template-columns: 1fr 1fr; } }

@media all and (min-width: 600px) {
  .pokemon-list {
    grid-template-columns: 1fr 1fr 1fr; } }

@media all and (min-width: 1000px) {
  .pokemon-list {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

.pagination-box {
  width: 100%; }

.pagination-per-page-box {
  display: inline-block;
  float: right;
  margin-right: 10px; }
  .pagination-per-page-box span {
    color: #464646; }

.pagination,
.pagination-per-page {
  list-style: none;
  display: inline-block; }
  .pagination li,
  .pagination-per-page li {
    display: inline-block;
    margin: 0 5px; }
    .pagination li a,
    .pagination-per-page li a {
      text-decoration: none;
      color: #464646; }
      .pagination li a:hover,
      .pagination-per-page li a:hover {
        color: #A4A5A5; }
    .pagination li.active a,
    .pagination-per-page li.active a {
      color: #F07B71; }



