.pie-chart {
  position: relative;
  width: 156px;
  margin: 0 auto; }
  .pie-chart path {
    cursor: pointer; }
    .pie-chart path:hover {
      opacity: 0.8; }
  .pie-chart .tooltip {
    color: #A4A5A5;
    display: none;
    font-size: 12px;
    left: 49px;
    position: absolute;
    text-align: center;
    top: 55px;
    width: 60px;
    z-index: 10; }
    .pie-chart .tooltip .label {
      text-transform: uppercase; }
    .pie-chart .tooltip .value {
      color: #464646;
      font-size: 16px;
      margin-top: 5px; }



