.pokemon-box {
  position: relative;
  min-width: 150px;
  background-color: #fff;
  border-radius: 10px;
  margin: 10px 0;
  height: 615px;
  overflow: hidden; }

.pokemon-detail {
  text-align: center; }

.pokemon-name {
  color: #A4A5A5;
  text-transform: uppercase; }

.pokemon-ava {
  border: 5px solid #ddd;
  border-radius: 50%;
  padding: 25px;
  margin-bottom: 15px; }

.pokemon-type {
  padding: 5px 10px;
  margin: 0 5px;
  cursor: pointer;
  background-color: #68B5E0;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px; }
  .pokemon-type:hover {
    opacity: 0.8; }

.pokemon-stat {
  padding: 5px;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-gap: 5px; }

.pokemon-stats {
  border-top: 4px solid #ddd;
  padding-top: 20px;
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  .pokemon-stats > * {
    margin-bottom: 10px; }
  .pokemon-stats dt {
    text-transform: uppercase;
    color: #ddd;
    font-size: 12px;
    -ms-flex-item-align: end;
    align-self: end; }
  .pokemon-stats dd {
    color: #A4A5A5;
    margin: 0; }



